$tablet-width:768px;
$desktop-width:1440px;

.container {
    width: 280px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
        width: 708px;
        margin: 0 auto;
    }

    @media (min-width: $desktop-width) {
        width: 1220px;
        margin: 0 auto;
    }
}



